<template>
     <div class="actionbar">

        <yd-grids-group :rows="3">
            <yd-grids-item>
                <span slot="text">Pass</span>
            </yd-grids-item>
            <yd-grids-item>
                <span slot="text">Like</span>
            </yd-grids-item>
            <yd-grids-item>
                <span slot="text">Invite</span>
            </yd-grids-item>
            
        </yd-grids-group>
    </div>
</template>
<script>
export default {
  
}
</script>

<style>
.actionbar .yd-grids-3:before, .actionbar .yd-grids-item:after{
    border: 0!important;
}
.actionbar .yd-grids-3{
    background: transparent!important;
}

.actionbar .yd-grids-txt span:hover{
    transform: scale(1.1);

}
.actionbar .yd-grids-3 .yd-grids-item:before{
    border: 0!important;
}
.actionbar .yd-grids-txt span{background: #fff; width: 60px; height: 60px; display: inline-block; border-radius: 50% ; vertical-align: middle; line-height: 60px;
font-weight: bold;
    -webkit-box-shadow: 0 4px 9px 0 rgba(213,218,224,.3);
    box-shadow: 0 4px 9px 0 rgba(213,218,224,.3);
    -webkit-transition: opacity .25s ease,-webkit-transform .25s ease,-webkit-filter .25s ease;
    transition: opacity .25s ease,-webkit-transform .25s ease,-webkit-filter .25s ease;
    transition: transform .25s ease,filter .25s ease,opacity .25s ease;
    transition: transform .25s ease,filter .25s ease,opacity .25s ease,-webkit-transform .25s ease,-webkit-filter .25s ease;}
.actionbar .yd-flexbox-item-center{
    background: #ffffff!important;
   
}

</style>




